  const updateSearchInput = useCallback(
    debounce((e: any) => {
      dispatch(setSearchValue(e));
    }, 350),
    [],
  );

  const searchInput = (e: any) => {
    setValue(e.target.value);
      updateSearchInput(e.target.value);
  };
  
